.scroll {
  width: 60px;
  height: 60px;
  border: 2px solid #f6d7ba;
  border-radius: 50%;
  position: relative;
  animation: down 1.5s infinite;
  -webkit-animation: down 1.5s infinite;
  &::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 18px;
    width: 18px;
    height: 18px;
    border-left: 2px solid #f6d7ba;
    border-bottom: 2px solid #f6d7ba;
    transform: rotate(-45deg);
  }
}
.titleflex {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 1em;
  line-height: 0.5%;
}

.planepile {
  padding-bottom: 0em;
  margin-left: 11em;
}
@keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}

@-webkit-keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}

@keyframes scroll {
  0% {
    height: 40px;
  }
  30% {
    height: 70px;
  }
  60% {
    height: 40px;
  }
}

@-webkit-keyframes scroll {
  0% {
    height: 40px;
  }
  30% {
    height: 70px;
  }
  60% {
    height: 40px;
  }
}

